.wizard .actions {
  position: relative;
  display: block;
  vertical-align: middle;
  *vertical-align: auto;
  *zoom: 1;
  *display: block;
  width: 100%;
  margin-top: 10px !important;
}

.wizard .actions ul {
  display: inline-block;
  vertical-align: middle;
  *vertical-align: auto;
  *zoom: 1;
  *display: inline;
}

.wizard .actions ul > li {
  float: left;
  margin: 0 !important;
}

.wizard .actions a {
  -webkit-transition: all 0.5s ease-out;
  -moz-transition: all 0.5s ease-out;
  -ms-transition: all 0.5s ease-out;
  -o-transition: all 0.5s ease-out;
  transition: all 0.5s ease-out;
  display: block;
  vertical-align: middle;
  *vertical-align: auto;
  *zoom: 1;
  *display: block;
  -webkit-border-radius: 35px;
  -moz-border-radius: 35px;
  border-radius: 35px;
  padding: 0.5em 2em;
  color: #FFFFFF;
  text-decoration: none;
}

.wizard .actions a:hover, .wizard .actions a:active {
  display: block;
  vertical-align: middle;
  *vertical-align: auto;
  *zoom: 1;
  *display: block;
  padding: 0.5em 2em;
  color: #FFFFFF;
  text-decoration: none;
}

.wizard .actions .disabled a {
  background: #e2e4e7;
  color: #bbbec2;
}

.wizard .actions .disabled a:hover, .wizard .actions .disabled a:active {
  background: #e2e4e7;
  color: #bbbec2;
}

.wizard .steps {
  position: relative;
  display: block;
  vertical-align: middle;
  *vertical-align: auto;
  *zoom: 1;
  *display: block;
  width: 100%;
  margin-bottom: 2px;
}

.wizard .steps a {
  display: block;
  vertical-align: middle;
  *vertical-align: auto;
  *zoom: 1;
  *display: block;
  width: auto;
  margin: 0 2px 0 0;
  padding: 10px;
  text-decoration: none;
  border-radius: 0;
}

.wizard .steps a:hover, .wizard .steps a:active {
  display: block;
  vertical-align: middle;
  *vertical-align: auto;
  *zoom: 1;
  *display: block;
  width: auto;
  margin: 0 2px 0 0;
  padding: 10px;
  text-decoration: none;
  border-radius: 0;
}

.wizard .steps .number {
  font-size: 15px;
}

.wizard .steps .disabled a {
  background: #2f3338;
  color: #96989c;
  cursor: default;
}

.wizard .steps .disabled a:hover, .wizard .steps .disabled a:active {
  background: #2f3338;
  color: #96989c;
  cursor: default;
}

.wizard .steps .current a {
  color: #FFFFFF;
