.timeline {
  line-height: 1.4em;
  list-style: none;
  width: 100%;
  /*--MOD: TIMELINE SPLIT-----------------------*/
  /*--MOD: TIMELINE CENTERED --------------------*/
  /*--MOD: MARKER OUTLINE-----------------------*/
}

.timeline .timeline-item {
  position: relative;
}

.timeline .timeline-info {
  letter-spacing: 3px;
  margin: 0 0 .5em 0;
  text-transform: uppercase;
  white-space: nowrap;
}

.timeline .timeline-marker {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 15px;
}

.timeline .timeline-marker:before {
  -webkit-border-radius: 100%;
  -moz-border-radius: 100%;
  border-radius: 100%;
  background: #00bcd2;
  border: 3px solid transparent;
  content: "";
  display: block;
  height: 15px;
  position: absolute;
  top: 4px;
  left: 0;
  width: 15px;
  transition: background 0.3s ease-in-out, border 0.3s ease-in-out;
}

.timeline .timeline-marker:after {
  content: "";
  width: 3px;
  background: rgba(255, 255, 255, 0.2);
  display: block;
  position: absolute;
  top: 24px;
  bottom: 0;
  left: 6px;
}

.timeline-item:last-child .timeline .timeline-marker:after {
  content: none;
}

.timeline .timeline-item:not(.period):hover .timeline-marker:before {
  background: transparent;
  border: 3px solid #00bcd2;
}

.timeline .timeline-content .timeline-title {
  font-size: 20px;
  font-weight: 400;
}

.timeline .period {
  padding: 0;
}

.timeline .period .timeline-info {
  display: none;
}

.timeline .period .timeline-marker:before {
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  border-radius: 0;
  background: transparent;
  content: "";
  width: 15px;
  height: auto;
  border: none;
  top: 0;
  bottom: 30px;
  position: absolute;
  border-top: 3px solid #57595d;
  border-bottom: 3px solid #57595d;
}

.timeline .period .timeline-marker:after {
  content: "";
  height: 32px;
  top: auto;
}

.timeline .period .timeline-content {
  padding: 40px 0 70px;
}

@media (min-width: 768px) {
  .timeline.timeline-split .timeline, .timeline.timeline-centered .timeline {
    display: table;
  }
  .timeline.timeline-split .timeline-item, .timeline.timeline-centered .timeline-item {
    display: table-row;
    padding: 0;
  }
  .timeline.timeline-split .timeline-info, .timeline.timeline-centered .timeline-info,
  .timeline.timeline-split .timeline-marker,
  .timeline.timeline-centered .timeline-marker,
  .timeline.timeline-split .timeline-content,
  .timeline.timeline-centered .timeline-content,
  .timeline.timeline-split .period .timeline-info,
  .timeline.timeline-centered .period .timeline-info {
    display: table-cell;
    vertical-align: top;
  }
  
  .timeline.timeline-split .timeline-marker,
  .timeline.timeline-centered .timeline-marker {
    position: relative;
  }
  
  .timeline.timeline-split .timeline-content,
  .timeline.timeline-centered .timeline-content {
    padding-left: 30px;
  }
  .timeline.timeline-split .timeline-info, .timeline.timeline-centered .timeline-info {
    padding-right: 30px;
  }
  .timeline.timeline-split .period .timeline-title, .timeline.timeline-centered .period .timeline-title {
    position: relative;
    left: -45px;
  }
}

@media (min-width: 992px) {
  .timeline.timeline-centered,
  .timeline.timeline-centered .timeline-item,
  .timeline.timeline-centered .timeline-info,
  .timeline.timeline-centered .timeline-marker,
  .timeline.timeline-centered .timeline-content {
    display: block;
    margin: 0;
    padding: 0;
  }
  .timeline.timeline-centered .timeline-item {
    padding-bottom: 40px;
    overflow: hidden;
  }
  .timeline.timeline-centered .timeline-marker {
    position: absolute;
    left: 50%;
    margin-left: -7.5px;
  }
  .timeline.timeline-centered .timeline-info,
  .timeline.timeline-centered .timeline-content {
